"use client";

import Link from "next/link";
import { useState } from "react";
import {
  Search,
  Menu,
  X,
  User,
  Heart,
  Sparkles,
  LogOut,
  Settings,
  Compass,
} from "lucide-react";
import { Container } from "@/components/ui/Container";
import { Button } from "@/components/ui/Button";
import { Avatar } from "@/components/ui/Avatar";
import { useAuthStore } from "@/store/authStore";

export function Header() {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isUserMenuOpen, setIsUserMenuOpen] = useState(false);
  const { user, isAuthenticated, logout } = useAuthStore();

  return (
    <header className="sticky top-0 z-50 w-full border-b border-border bg-canvas/80 backdrop-blur-xl">
      <Container className="flex h-16 items-center justify-between gap-4">
        {/* Logo */}
        <Link
          href="/"
          className="flex items-center gap-2 font-semibold text-lg text-fg-on-canvas hover:text-fg transition-colors"
        >
          <div className="h-8 w-8 rounded-full overflow-hidden border border-border bg-canvas-subtle">
            <img
              src="https://res.cloudinary.com/dqwbkjfuh/image/upload/v1764588498/Screenshot_2025-11-26_at_6.58.01_PM_m1stjx.png"
              alt="Logo"
              className="h-full w-full object-cover"
            />
          </div>
          <span className="hidden sm:block">blr-thrifter</span>
        </Link>

        {/* Center Nav - Desktop */}
        <nav className="hidden md:flex items-center gap-1">
          <Link href="/">
            <Button variant="ghost" size="sm">
              <Compass className="h-4 w-4 mr-1.5" />
              Discover
            </Button>
          </Link>
          <Link href="/explore">
            <Button variant="ghost" size="sm">
              <Sparkles className="h-4 w-4 mr-1.5" />
              For You
            </Button>
          </Link>
          <Link href="/search">
            <Button variant="ghost" size="sm">
              <Search className="h-4 w-4 mr-1.5" />
              Search
            </Button>
          </Link>
        </nav>

        {/* Right Actions */}
        <div className="flex items-center gap-2">
          {/* Search Button - Mobile */}
          <Link href="/search" className="md:hidden">
            <Button variant="ghost" size="icon">
              <Search className="h-5 w-5" />
            </Button>
          </Link>

          {isAuthenticated && user ? (
            <>
              {/* Favorites */}
              <Link href="/favorites" className="hidden sm:block">
                <Button variant="ghost" size="icon">
                  <Heart className="h-5 w-5" />
                </Button>
              </Link>

              {/* User Menu */}
              <div className="relative">
                <button
                  onClick={() => setIsUserMenuOpen(!isUserMenuOpen)}
                  className="flex items-center gap-2 p-1 rounded-full hover:bg-canvas-subtle transition-colors"
                >
                  <Avatar
                    src={user.avatar}
                    name={user.name}
                    size="sm"
                  />
                </button>

                {isUserMenuOpen && (
                  <>
                    <div
                      className="fixed inset-0"
                      onClick={() => setIsUserMenuOpen(false)}
                    />
                    <div className="absolute right-0 top-full mt-2 w-56 py-2 bg-canvas-subtle border border-border rounded-xl shadow-xl animate-scaleIn origin-top-right">
                      <div className="px-4 py-2 border-b border-border">
                        <p className="text-sm font-medium text-fg-on-canvas truncate">
                          {user.name}
                        </p>
                        <p className="text-xs text-fg-muted truncate">
                          {user.email}
                        </p>
                      </div>
                      <div className="py-1">
                        <Link
                          href="/profile"
                          className="flex items-center gap-2 px-4 py-2 text-sm text-fg hover:bg-canvas-overlay transition-colors"
                          onClick={() => setIsUserMenuOpen(false)}
                        >
                          <User className="h-4 w-4" />
                          Profile
                        </Link>
                        <Link
                          href="/favorites"
                          className="flex items-center gap-2 px-4 py-2 text-sm text-fg hover:bg-canvas-overlay transition-colors"
                          onClick={() => setIsUserMenuOpen(false)}
                        >
                          <Heart className="h-4 w-4" />
                          Favorites
                        </Link>
                        <Link
                          href="/settings"
                          className="flex items-center gap-2 px-4 py-2 text-sm text-fg hover:bg-canvas-overlay transition-colors"
                          onClick={() => setIsUserMenuOpen(false)}
                        >
                          <Settings className="h-4 w-4" />
                          Settings
                        </Link>
                      </div>
                      <div className="pt-1 border-t border-border">
                        <button
                          onClick={() => {
                            logout();
                            setIsUserMenuOpen(false);
                          }}
                          className="flex items-center gap-2 w-full px-4 py-2 text-sm text-red-400 hover:bg-canvas-overlay transition-colors"
                        >
                          <LogOut className="h-4 w-4" />
                          Sign Out
                        </button>
                      </div>
                    </div>
                  </>
                )}
              </div>
            </>
          ) : (
            <>
              <Link href="/login" className="hidden sm:block">
                <Button variant="ghost" size="sm">
                  Sign In
                </Button>
              </Link>
              <Link href="/signup">
                <Button variant="primary" size="sm">
                  Get Started
                </Button>
              </Link>
            </>
          )}

          {/* Mobile Menu Button */}
          <Button
            variant="ghost"
            size="icon"
            className="md:hidden"
            onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
          >
            {isMobileMenuOpen ? (
              <X className="h-5 w-5" />
            ) : (
              <Menu className="h-5 w-5" />
            )}
          </Button>
        </div>
      </Container>

      {/* Mobile Menu */}
      {isMobileMenuOpen && (
        <div className="md:hidden border-t border-border bg-canvas animate-slideDown">
          <Container className="py-4">
            <nav className="flex flex-col gap-1">
              <Link
                href="/"
                onClick={() => setIsMobileMenuOpen(false)}
                className="flex items-center gap-2 px-4 py-3 rounded-lg hover:bg-canvas-subtle transition-colors"
              >
                <Compass className="h-5 w-5 text-fg-muted" />
                <span>Discover</span>
              </Link>
              <Link
                href="/explore"
                onClick={() => setIsMobileMenuOpen(false)}
                className="flex items-center gap-2 px-4 py-3 rounded-lg hover:bg-canvas-subtle transition-colors"
              >
                <Sparkles className="h-5 w-5 text-fg-muted" />
                <span>For You</span>
              </Link>
              <Link
                href="/search"
                onClick={() => setIsMobileMenuOpen(false)}
                className="flex items-center gap-2 px-4 py-3 rounded-lg hover:bg-canvas-subtle transition-colors"
              >
                <Search className="h-5 w-5 text-fg-muted" />
                <span>Search</span>
              </Link>
              {isAuthenticated && (
                <Link
                  href="/favorites"
                  onClick={() => setIsMobileMenuOpen(false)}
                  className="flex items-center gap-2 px-4 py-3 rounded-lg hover:bg-canvas-subtle transition-colors"
                >
                  <Heart className="h-5 w-5 text-fg-muted" />
                  <span>Favorites</span>
                </Link>
              )}
              {!isAuthenticated && (
                <Link
                  href="/login"
                  onClick={() => setIsMobileMenuOpen(false)}
                  className="flex items-center gap-2 px-4 py-3 rounded-lg hover:bg-canvas-subtle transition-colors sm:hidden"
                >
                  <User className="h-5 w-5 text-fg-muted" />
                  <span>Sign In</span>
                </Link>
              )}
            </nav>
          </Container>
        </div>
      )}
    </header>
  );
}

